<template>
    <div id="single-blog">
        <h1>{{blog.title}}</h1>
        <article>{{blog.content}}</article>
        <p>作者：{{blog.author}}</p>
        <P>分类：</P>
        <ol>
          <li v-for="category in blog.categories" :key="category">
            {{category}}
          </li>
        </ol>
        <router-link :to="'/edit/' + id">编辑</router-link>
        <button @click="deletSingleBlog()">删除</button>
    </div>
</template>
<script>
export default {
  name: "single-blog",
  data() {
    return {
      id: this.$route.params.id,
      blog: {}
    };
  },
  created() {
    this.$http
      .get("https://wd6889473708jphohe.wilddogio.com/posts/" + this.id + ".json")
      .then(function(data) {
        console.log(data);
        return data.json();
      })
      .then(function(data) {
        this.blog = data;
      });
  },
  methods: {
    deletSingleBlog() {
      this.$http
        .delete(
          "https://wd6889473708jphohe.wilddogio.com/posts/" + this.id + ".json"
        )
        .then(response => {
          this.$router.push({ path: "/" });
        });
    }
  }
};
</script>
<style scoped>
#single-blog {
  max-width: 960px;
  margin: 0 auto;
  padding: 20px;
  background: rgb(185, 128, 128);
  border: 1px dotted #aaa;
}
button{
  cursor: pointer;
}
a{
  display: inline-block;
  width: 50px;
  height: 20px;
  text-decoration: none;
  color: #000;
  font-size: 16px;
  background: #eee;
  text-align: center;
  line-height: 20px;
}
</style>




